<template>
  <div class='myHeader'>
    <div class="header">
        <div class="header-user">
            <p @click='goback'>
                <img src="~assets/img/logo.png">
            </p>
            <p @click='showUserMsg' id="userName" class="userName">{{userMsg[0].val}}</p>
        </div>
        <div class="header-content">
            <div class="search-div clearfloat">
                <div class="search-block">
                    <a @click='getLicenceInfo' title="Licence信息" class="licence-a">
                        <i class="warning-icon iconfont icon-jihuo"></i>
                    </a>
                    <a @click='goback' title="返回首页" class="licence-a">
                        <i class="warning-icon iconfont icon-fanhuishouye1"></i>
                    </a>
                    <a @click='openSets' title="过滤设置">
                        <i class="warning-icon iconfont icon-zidingyishezhi"></i>
                    </a>
                    <a class="search-btn checkData-btn refresh-data-btn" @click="findAll">重新获取数据</a>
                    <a class="search-btn checkData-btn" @click="goAnother" v-show="navShow">查看数据</a>
                    <a class="log_out" @click="loginOut()">退出账号</a>
                </div>
            </div>
        </div>
    </div>
    <!-- 用户信息弹窗-->
    <div v-show='isAlertUser' class='alertBoxHeader'>
      <div class='alertUser'>
        <div class='alertUserTitle'>
          当前用户
          <i @click='closeAlertUser' class='iconfont icon-zhongzi-guanbi closeAlertUser'></i>
        </div>
        <ul>
          <li v-for='item in userMsg' class='userItem'>
            <div class='userMsgTitle'>{{item.title}}</div>
            <div class='userMsgText'>{{item.val}}</div>
          </li>
        </ul>
      </div>
    </div>
    <!-- license信息弹窗-->
    <div v-show='isAlertLicense' class='alertBoxHeader'>
          <div class='alertUser alertLicense'>
              <div class='alertUserTitle'>
                  License信息
                  <i @click='closeAlertLicense' class='iconfont icon-zhongzi-guanbi closeAlertUser'></i>
              </div>
              <ul>
                  <li v-for='item in licenseMsg' class='licenseItem'>
                      <div class="licenseTitleMsg">{{item.title}}:</div>
                      <div class="licenseTitleText">{{item.val}}</div>
                  </li>
              </ul>
              <div class="btn-block">
                  <button class="sure-btn" type="button" @click='closeAlertLicense'>确定</button>
              </div>
          </div>
    </div>
    <!-- 自定义设置 -->
    <div v-show='isAlertSets' class='alertBoxHeader'>
        <div class='alertUser alertSets'>
              <div class='alertUserTitle'>
                  过滤设置
                  <i @click='closeAlertSets' class='iconfont icon-zhongzi-guanbi closeAlertUser'></i>
              </div>
              <div class="alertContentSets">
                  <div class="block">
                      <label class="label">选择组织</label>
                      <treeselect class="select" v-model="chooseTid" :multiple="true" :options="treeList"  placeholder="请选择组织" @input="changeTree"/>
                  </div>
                  <div class="block">
                      <label class="label">组织类型</label>
                      <treeselect class="select" v-model="chooseTroot" :multiple="true" :options="treeTypeList"  placeholder="请选择组织类型" @input="changeTreeType"/>
                  </div>
                  <div class="block">
                      <label class="label">采集器类型</label>
                      <treeselect class="select" v-model="chooseDgm" :multiple="true" :options="dgmTypeList"  placeholder="请选择采集器类型" @input="changeDgmType"/>
                  </div>
                  <div class="customContent">
                      <div class="customTitle"><span @click="toggleBlock"><i class="iconfont" :class="showSets?'icon-jianshao':'icon-zengjia'"></i> 展开自定义设置</span></div>
                      <div class="customBlock" v-show="showSets">
                          <div class="customContainer" v-for="(part,index) in costoms" :key="index">
                            <div class="customPanel" @click="togglePanel(part)"><span>{{part.part}}</span><i class="iconfont" :class="part.showChild?'icon-shouqi1':'icon-xiala1'"></i></div>
                            <div class="customPart" v-for="(nav,index) in part.partList" :key="index" v-show="part.showChild">
                                <div class="customListTitle">{{nav.nav}}</div>
                                <div class="customList">
                                    <div class="checkBoxDiv" v-for="(item,index) in nav.btnData" :key="index">
                                        <input type="checkbox" v-model="item.isShow" :id="item.textBtn" ><label class="checkBoxLabel" :for="item.textBtn">{{item.textBtn}}</label>
                                    </div>
                                </div>
                            </div>
                          </div>
                      </div>
                  </div>
              </div>
              <div class="alertFooter">
                  <button class="sure-btn" type="button" @click='changeSets'>确定</button>
                  <button class="no-btn" type="button" @click='closeAlertSets'>取消</button>
              </div>
          </div>
    </div>
  </div>
</template>

