          ],
          chooseTroot:null,//所选组织类型
          chooseDgm:null,//选择的采集器类型
          dgmTypeList:[//采集器类型下拉列表
              {id: 1, label: "DGU2000V1"},
              {id: 2, label: "DGU2200"},
              {id: 3, label: "DGU2000V2"},
