                {type:13,textBtn:"螺栓采集设置",name:'gatherch',isDgm: true,isShow: true},
                {type:14,textBtn:"法兰设置",name:'flangech',isDgm: true,isShow: true},
                {type:15,textBtn:"预处理器设置",name:'prepsch',isDgm: true,isShow: true},
