                {type:17,textBtn:"E821从设备",name:'prinsub821',isDgm: true,isShow: true},
                {type:18,textBtn:"外部集成设置",name:'ExteInter',isDgm: true,isShow: true},
                {type:19,textBtn:"轨道波磨采样",name:'railch',isDgm: true,isShow: true},
          ],
