                {type: 3, textBtn: "振动测点",name:'vib',isMachine:true,isShow: true},
                {type: 4, textBtn: "包络测点",name:'gd',isMachine:true,isShow: true},
                {type: 15, textBtn: "冲击测点" ,name:"shock",isMachine:true,isShow: true},
