  created() {
      this.getTreeData();//获取组织信息
      this.resultMacBtn = this.oriMacBtn;
      this.resultDgmBtn = this.oriDgmBtn;
      this.resultPosBtn = this.oriPosBtn;
      this.resultAlarmBtn = this.oriAlarmBtn;
      this.resultMonitorBtn = this.oriMonitorBtn;
      this.resultRegulateBtn = this.oriRegulateBtn;
      this.setCustomData();
      this.getCustomInfo();//获取自定义过滤设置信息
  },
  mounted() {
      this.token = sessionStorage.getItem('token');
  },
  computed: {
      navShow() {
          if(this.type===0 && this.username==='manager'){
              return false;
          }
          else{
              return true
          }
      },
  },
  methods: {
    // 获取用户过滤设置信息
    getCustomInfo(){
        let params = {
            userId:this.userId,
        };
        this.$axios.post('getUserConfig',params)
            .then(res => {
                if(res.msg===0){
                    let config = res.config;
                    if(config){
                        if(config.chooseTid != undefined){
                            this.chooseTid = config.chooseTid;
                        }
                        if(config.chooseTroot != undefined){
                            this.chooseTroot = config.chooseTroot;
                        }
                        if(config.chooseDgm != undefined){
                            this.chooseDgm = config.chooseDgm;
                        }
                        if(config.chooseMacBtn != undefined){
                            this.resultMacBtn = config.chooseMacBtn;
                        }
                        if(config.chooseDgmBtn != undefined){
                            this.resultDgmBtn = config.chooseDgmBtn;
                        }
                        if(config.choosePosBtn != undefined){
                            this.resultPosBtn = config.choosePosBtn;
                        }
                        if(config.chooseAlarmBtn != undefined){
                            this.resultAlarmBtn = config.chooseAlarmBtn;
                        }
                        if(config.monitorBtn != undefined){
                            this.resultMonitorBtn = config.monitorBtn;
                        }
                        if(config.regulateBtn != undefined){
                            this.resultRegulateBtn = config.regulateBtn;
                        }
                        this.setCustomData();
                        if(this.chooseTid && this.chooseTid.length){
                            sessionStorage.setItem('getTidList',JSON.stringify(this.chooseTid))
                            this.$store.commit('setTids', this.chooseTid);
                        }
                        else{
                            sessionStorage.setItem('getTidList',this.$store.state.origTids)
                            this.$store.commit('setTids', this.$store.state.origTids);
                        }
                        this.$store.commit('setCustomInfo', config)
                    }
                }
                else{
                    console.log('请求失败')
                }
                }).catch(err => {
                    console.log(err)
            })
        
    },
    //造按钮的数据
    setBtnData(oriData,list){
        let result = [];
        oriData.forEach(item=>{
            if(list.includes(item.type)){
                result.push(item)
            }
        });
        return result;
    },
    //造自定义选项的数据
    setCustomData(){
        this.costoms.forEach(part=>{
            let list = part.partList;
            list.forEach(item=>{
                if(item.nav==='机组设置'){
                    item.btnData = this.resultMacBtn;
                }
                if(item.nav==='采集器设置'){
                    item.btnData = this.resultDgmBtn;
                }
                if(item.nav==='测点设置'){
                    item.btnData = this.resultPosBtn;
                }
                if(item.nav==='报警设置'){
                    item.btnData = this.resultAlarmBtn;
                }
                if(item.nav==='设备监听配置'){
                    item.btnData = this.resultMonitorBtn;
                }
                if(item.nav==='校正系数配置'){
                    item.btnData = this.resultRegulateBtn;
                }
            })
        })                            
    },
    //获取组织数据
    getTreeData() {
        this.treeList = [];
        let params = null;
        params={
            type:2,
            t_ids:this.$store.state.origTids,
        };
        this.$axios.post('getPositionDataConfig',params)
                .then(res => {
                    if(res.msg===0 && res.list && res.list.length){
                        this.treeList = this.getFolderList(res.list);
                    }
                    else{
                        console.log('请求失败')
                    }
                }).catch(err => {
                    console.log(err)
                })
    },
    //递归组织
    getFolderList(list){
        if(list && list.length){
            list.forEach(item=>{
                item.id = item.t_id.toString();
                item.label = item.t_name;
                item.status = false;
                if(item.children){
                    this.getFolderList(item.children);
                }
            })
        }
        return list;
    },
    //   打开自定义设置界面
    openSets(){
        this.isAlertSets = true;
    },
    // 关闭自定义设置界面
    closeAlertSets(){
        this.isAlertSets = false;
    },
    // 修改自定义设置
    changeSets(){
        this.costoms.forEach(part=>{
            let list = part.partList;
            list.forEach(item=>{
                let btnList = item.btnData;
                if(item.nav==='机组设置'){
                    this.chooseMacBtn = btnList;
                }
                if(item.nav==='采集器设置'){
                    this.chooseDgmBtn = btnList;
                }
                if(item.nav==='测点设置'){
                    this.choosePosBtn = btnList;
                }
                if(item.nav==='报警设置'){
                    this.chooseAlarmBtn = btnList;
                }
                if(item.nav==='设备监听配置'){
                    this.monitorBtn = btnList;
                }
                if(item.nav==='校正系数配置'){
                    this.regulateBtn = btnList;
                }
            })
        })
        let macFlag = this.chooseMacBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            }); 
        let dgmFlag = this.chooseDgmBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            });  
        let posFlag = this.choosePosBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            }); 
        let alarmFlag = this.chooseAlarmBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            }); 
        let monitorFlag = this.monitorBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            }); 
        let regulateFlag = this.regulateBtn.every((item) => {
                if (!item.isShow) {
                    return true;
                }
            }); 
        
        if(macFlag){
            swal({
                title: "提示",
                text: `请至少选择一个机组设置选项`,
                button: "确认",
            });
            return false;
        }
        if(dgmFlag){
            swal({
                title: "提示",
                text: `请至少选择一个采集器设置选项`,
                button: "确认",
            });
            return false;
        }
        if(posFlag){
            swal({
                title: "提示",
                text: `请至少选择一个测点设置选项`,
                button: "确认",
            });
            return false;
        }
        if(alarmFlag){
            swal({
                title: "提示",
                text: `请至少选择一个报警设置选项`,
                button: "确认",
            });
            return false;
        }
        if(monitorFlag){
            swal({
                title: "提示",
                text: `请至少选择一个设备监听设置选项`,
                button: "确认",
            });
            return false;
        }
        if(regulateFlag){
            swal({
                title: "提示",
                text: `请至少选择一个校正系数设置选项`,
                button: "确认",
            });
            return false;
        }
        let params = {
            userId:this.userId,
            chooseTid:this.chooseTid,
            chooseTroot:this.chooseTroot,
            chooseDgm:this.chooseDgm,
            chooseMacBtn:this.chooseMacBtn,
            chooseDgmBtn:this.chooseDgmBtn,
            choosePosBtn:this.choosePosBtn,
            chooseAlarmBtn:this.chooseAlarmBtn,
            monitorBtn:this.monitorBtn,
            regulateBtn:this.regulateBtn,
        };
        this.$axios.post('updateUserConfig',params)
            .then(res => {
                if(res.msg===0){
                    swal({
                        title: "提示",
                        text: "设置成功",
                        button: "确认",
                    }); 
                    this.isAlertSets = false;
                    this.getCustomInfo();
                }
                else{
                    swal({
                        title: "提示",
                        text: "设置失败",
                        button: "确认",
                    }); 
                    console.log('请求失败')
                }
                }).catch(err => {
                    console.log(err)
            })            
    },
    // 选择组织
    changeTree(){

    },
    // 改变组织类型
    changeTreeType(node){
        this.resultMacBtn = this.getTypeBtns(node,this.macTypeObj,this.oriMacBtn);
        this.setCustomData();
        this.$forceUpdate();
    },
    // 改变采集器类型
    changeDgmType(node){
        this.resultDgmBtn = this.getTypeBtns(node,this.dgmTypeObj,this.oriDgmBtn);
        this.resultPosBtn = this.getTypeBtns(node,this.posTypeObj,this.oriPosBtn);
        this.resultAlarmBtn = this.getTypeBtns(node,this.alarmTypeObj,this.oriAlarmBtn);
        this.resultMonitorBtn = this.getTypeBtns(node,this.monitorTypeObj,this.oriMonitorBtn);
        this.resultRegulateBtn = this.getTypeBtns(node,this.regulateTypeObj,this.oriRegulateBtn);
        this.setCustomData();
        this.$forceUpdate();
    },
    /*
    *  根据选择的采集器类型确定:采集器设置按钮/测点设置/报警设置/采集器监测设置的可选项
    *  typeList：所选择的采集器类型集合
    *  typeObj：采集器类型对应的按钮选项对象
    *  oriBtn：全部按钮可选项
    *  
    */
    getTypeBtns(typeList,typeObj,oriBtn){
        let arr = [];//可供选择的采集器通道设置按钮集合
        if(typeList.length){
            typeList.forEach(type=>{
                let btnTypeList = typeObj[type];//获取这种类型的采集器器下的通道类型
                oriBtn.forEach(item=>{
                    if(btnTypeList.includes(item.type)){
                        arr.push(item)
                    }
                })
            })
        }
        else{
            arr = oriBtn.concat();
        }
        arr = this.unique(arr);//去重
        return arr;
    },
    // toggle效果
    togglePanel(part){
        part.showChild = !part.showChild;
    },
    // 是否展示自定义内容
    toggleBlock(){
        this.showSets = !this.showSets;
    },
    findAll(){
        // sessionStorage.clear();
      sessionStorage.removeItem('filter_alarm1');
      sessionStorage.removeItem('filter_alarm2');
      sessionStorage.removeItem('filter_alarm3');
      // sessionStorage.removeItem('dgm_id');
      // sessionStorage.removeItem('dgm_names');
      sessionStorage.removeItem('filter_type1');
      sessionStorage.removeItem('filter_type2');
      sessionStorage.removeItem('filter_type3');
      sessionStorage.removeItem('filter_dgm_name');
      sessionStorage.removeItem('filter_mac_name');
      sessionStorage.removeItem('filter_tree_name');
      sessionStorage.removeItem('LinkConfig');
      this.$router.go(0);
    },
    goAnother(){
        let routeUrl = this.$router.resolve({
            path: "/viewInterface",
        });
        window.open(routeUrl.href, '_blank');
    },
    goback:function(){
        this.$router.push({name: "home"})
    },
    getLicenceInfo(){//获取licence信息
        //获取licence信息
        this.isAlertLicense = true;
        this.$getApi.getLicense()
            .then(res=>{
                let days = Math.ceil(res.validTime/1000/3600/24);
                this.licenseMsg[0].val = res.uuid;
                this.licenseMsg[1].val = days + '天';
                this.licenseMsg[2].val = res.num;
            })
            .catch(function(err) {
                console.log('failed', err);
                swal({
                    title: "提示",
                    text: `请求错误`,
                    button: "确认",
                });
            });
    },
    loginOut: function () {
        this.$router.push({name:'login'});
        this.$axios.post('loginOut',this.token)
          .then(res=>{
              sessionStorage.clear();
            switch (parseInt(res.msg)) {
                    case 1:
                        console.log("token为null");
                        break;
            }
          })
            .catch(err=>{
                console.log('请求错误',err)
            })

    },
    // 显示查询当前用户信息
    showUserMsg() {
      this.isAlertUser = true;
    },
    //  关闭查询当前用户信息
    closeAlertUser() {
      this.isAlertUser = false;
    },
    //关闭licence信息
    closeAlertLicense() {
        this.isAlertLicense = false;
    },
    //数组去重
    unique(arr){
        const res = new Map();
        return arr.filter((a) => !res.has(a) && res.set(a, 1))
    },
  },
}

</script>

