<template>
  <div>
      <my-header  id='homeHeader' :userMsg='userMsg'></my-header>
      <div id='homeContents'>
          <div id="aside">
             <my-side :activeNum="mainActiveNum"></my-side>
          </div>
          <div id="content">
              <my-contents  @ievent="listenTo"> </my-contents>
          </div>
      </div>
  </div>
</template>

<script>
import MyHeader from 'components/MyHeader'
import MySide from 'components/MySide'
import MyContents from 'components/MyContents'


export default {
  name: "Home",
  data(){
    return{
      token: '',
      mainActiveNum: 0,
      userMsg: [
          {
              title: '',
              val: '',
          },
          {
              title: '',
              val: '',
          },
      ],
    }
  },
  created(){
      sessionStorage.getItem('getTidList') && this.$store.commit('setTids', JSON.parse(sessionStorage.getItem('getTidList')))
      sessionStorage.getItem('getTidList') && this.$store.commit('setOrigTids', JSON.parse(sessionStorage.getItem('getTidList')))
  },
  mounted() {
      let name = sessionStorage.getItem('username');
      let userType = sessionStorage.getItem('userType');
      this.userMsg = [
        {
            title: '用户名称',
            val: name,
        
        }, {
            title: '用户类型',
            val: this.setUserType(userType),
      }];
  },
  methods: {
    // 判断用户类型
    setUserType(type) {
      const userType = ['超级管理员', 'CMC管理员', 'CMC操作员','HMI操作员'];
      return userType[type];
    },
    listenTo: function(activeNum) {
        this.mainActiveNum = activeNum;
    },
  },
  components: {
    MyHeader,
    MySide,
    MyContents
  }

}

</script>

<style  lang='scss'>
#homeHeader{
  height: 80px;
}
#homeContents{
  display: flex;
  // flex: 1 1 auto;
  flex-flow:row;/*伸缩项目单行排列*/
  background: #e4e8f4;
}
#aside{
    flex:0 0 160px;
}
#content{
  padding: 20px 32px;
  height: calc(100vh - 120px);
  flex:1 1 1px;
}
@media screen and (max-height: 800px) {
    #homeHeader{
        height: 70px;
    }
    #content{
        padding: 10px;
        height: calc(100vh - 90px);
        flex:1 1 1px;
    }
}
</style>


