<template>
    <router-view  v-if="isRouterAlive" class="myContentArea" ></router-view>
</template>

<script>
export default {
  name: "MyContent",
  provide () {
    return {
      reload: this.reload
    }
  },
  data() {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload(){
       this.isRouterAlive = false
       this.$nextTick(() => {
        this.isRouterAlive = true
      })

    }
  },
  components: {
  },
}

</script>

<style scoped lang='scss'>

  .myContentArea{
    height: 100%;
  }




</style>
