<template>
  <div id="home_aside">
    <div class="aside-bar">
      <ul class="aside-nav">
          <li class="aside-item" data-nav="0" v-show="navShow">
              <router-link :to="router1" active-class="active"><i class="nav-icon iconfont icon-xitongguanli"></i><span class="nav-name">系统管理</span><i class="nav-icon2 iconfont icon-xitongguanli"></i></router-link>
          </li>
          <li class="aside-item" data-nav="1" v-show="navShow">
              <router-link to="/home/device" active-class="active"><i class="nav-icon iconfont icon-shuzishuangbaotai"></i><span class="nav-name">数字双胞胎</span><i class="nav-icon2 iconfont icon-shuzishuangbaotai"></i></router-link>
          </li>
          <li class="aside-item" data-nav="2" v-show="navShow">
              <router-link  :to="router2" active-class="active"><i class="nav-icon iconfont icon-zongmaotushezhi"></i><span class="nav-name">总貌图设置</span><i class="nav-icon2 iconfont icon-zongmaotushezhi"></i></router-link>
          </li>
          <li class="aside-item" data-nav="3" v-show="navShow">
              <router-link to="/home/level" active-class="active"><i class="nav-icon iconfont icon-shangxiajishezhitubiaogenggai"></i><span class="nav-name">上下级设置</span><i class="nav-icon2 iconfont icon-shangxiajishezhitubiaogenggai"></i></router-link>
          </li>
          <li class="aside-item" data-nav="4" v-show="navShow">
              <router-link  :to="router3" active-class="active"><i class="nav-icon iconfont icon-zongmaotushezhi"></i><span class="nav-name">辅助功能</span><i class="nav-icon2 iconfont icon-zongmaotushezhi"></i></router-link>
          </li>
          <li class="aside-item" data-nav="6" v-show="wisdomNav && navShow">
              <router-link :to="router4" active-class="active"><i class="nav-icon iconfont icon-zhizi"></i><span class="nav-name">智子设置</span><i class="nav-icon2 iconfont icon-zhizi"></i></router-link>
          </li>
          <li class="aside-item" data-nav="5" v-show="type==0">
              <router-link to="/home/user" active-class="active"><i class="nav-icon iconfont icon-zhanghushezhitubiaogenggai"></i><span class="nav-name">账户设置</span><i class="nav-icon2 iconfont icon-zhanghushezhitubiaogenggai"></i></router-link>
          </li>
      </ul>
    </div>
    <ul class="child-list child-list-0" data-list="0" v-show="navShow">
        <router-link to="/home/folder" active-class="active"></router-link>
        <li class="child-name"><router-link to="/home/folder" active-class="active">组织设置</router-link></li>
        <li class="child-name"><router-link to="/home/machine" active-class="active">机组设置</router-link></li>
        <li class="child-name"><router-link to="/home/dgmBind" active-class="active">采集器绑定</router-link></li>
        <li class="child-name"><router-link to="/home/dgm" active-class="active">采集器设置</router-link></li>
        <li class="child-name"><router-link to="/home/position" active-class="active">测点设置</router-link></li>
        <li class="child-name"><router-link to="/home/alarm" active-class="active">报警设置</router-link></li>
        <li class="child-name"><router-link to="/home/dgmUp" active-class="active">采集器升级</router-link></li>
        <li class="child-name"><router-link to="/home/ExAndIm" active-class="active">导入导出</router-link></li>

        <div class="toggle-btn-list"><img src="~assets/img/open.svg"></div>
    </ul>
    <ul class="child-list child-list-2" data-list="2" v-show="navShow">
        <li class="child-name"><router-link to="/home/overview" active-class="active">组织设置</router-link></li>
        <li class="child-name"><router-link to="/home/overviewPump" active-class="active">机泵设置</router-link></li>
        <div class="toggle-btn-list"><img src="~assets/img/open.svg"></div>
    </ul>
    <ul class="child-list child-list-3" data-list="4" v-show="navShow">
        <li class="child-name"><router-link to="/home/monitor" active-class="active">设备监听配置</router-link></li>
        <li class="child-name"><router-link to="/home/AutoExport" active-class="active">报告自动导出</router-link></li>
        <li class="child-name"><router-link to="/home/dataDilute" active-class="active">数据稀释配置</router-link></li>
        <li class="child-name"><router-link to="/home/regulate" active-class="active">校正系数配置</router-link></li>
        <li class="child-name" v-show="type==0"><router-link to="/home/hmiSet" active-class="active">HMI图谱设置</router-link></li>
        <!--<li class="child-name"><router-link to="/home/datas" active-class="active">modbus协议</router-link></li>
        <li class="child-name"><router-link to="/home/Opc" active-class="active">OPC配置</router-link></li>-->
        <div class="toggle-btn-list"><img src="~assets/img/open.svg"></div>
    </ul>
    <ul class="child-list child-list-5" data-list="6" v-show="wisdomNav && navShow">
        <li class="child-name"><router-link to="/home/rule" active-class="active">智子规则设置</router-link></li>
        <li class="child-name"><router-link to="/home/filterRule" active-class="active">过滤规则设置</router-link></li>
        <li class="child-name"><router-link to="/home/intelligent" active-class="active">智子设置</router-link></li>
        <li class="child-name"><router-link to="/home/setIntelligent" active-class="active">全局开关</router-link></li>
        <div class="toggle-btn-list"><img src="~assets/img/open.svg"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "MySide",
  props: ["parentToChild","activeNum"],
    data() {
        return {
            wisdomNav:true,
            type:Number(sessionStorage.getItem('userType')),
            username:sessionStorage.getItem('username'),
            router1:'/home/folder',
            router2:'/home/overview',
            router3:'/home/monitor',
            router4:'/home/intelligent',
        }
    },
    created(){
        this.wisdomNav=config.intelligentNav;
    },
    mounted(){
        $('.aside-item').click(function (e) {
            e.stopPropagation();
            /*$(this).siblings('.aside-item').removeClass('active');
            $(this).addClass('active');*/
            var indexThis = parseInt($(this).attr('data-nav'));
            let childList = [0,2,4,6];
            if(childList.indexOf(indexThis)<0){
                $('.aside-bar').removeClass('aside-bar-move');
            }
            else{
                $('.child-list').each(function () {
                    let that = $(this);
                    var dataList = parseInt($(this).attr('data-list'));
                    if(indexThis==dataList){
                        if ($('.aside-bar').hasClass('aside-bar-move')){
                            $('.aside-bar').removeClass('aside-bar-move');
                            $(this).removeClass('child-list-move');
                            $(this).siblings('.child-list').addClass('child-list-move');
                        }else {
                            $('.aside-bar').addClass('aside-bar-move');
                            $(this).addClass('child-list-move');
                            $(this).siblings('.child-list').removeClass('child-list-move');
                        }
                    }

                    $(this).find('.toggle-btn-list').click(function () {
                        that.removeClass('child-list-move');
                        $('.aside-bar').removeClass('aside-bar-move');
                    })
                })
            }

        });
    },
    computed: {
        navShow() {
            //当前用户只需看到用户配置界面，其他界面隐藏
            if(this.type===0 && this.username==='manager'){
                return false;
            }
            else{
                return true
            }
        },
    },
    methods:{
        fetchData(){
            let router = this.$route.path;
            let routerBase = router.split('/')[2];
            let routerName = this.$route.name;
            if(routerName == 'overview' || routerName == 'overviewPump'){
                this.router2 = '/home/' + routerName;
            }
            else{
                this.router2 = '/home/overview'
            }

            if(routerBase == 'folder' || routerBase == 'machine' || routerBase == 'position' || routerBase == 'alarm' || routerBase == 'dgm' || routerBase == 'dgmUp' || routerBase == 'dgmBind' || routerBase == 'ExAndIm'){
                this.router1 = router;
            }
            else{
                this.router1 = '/home/folder'
            }

            if(routerBase == 'datas' || routerBase == 'OpcClient' || routerBase == 'Monitor' || routerBase == 'AutoExport' || routerBase == 'regulate' || routerBase == 'dataDilute' || routerBase == 'Opc'){
                this.router3 = router;
            }
            else{
                this.router3 = '/home/monitor'
            }

            if(routerName == 'rule' || routerName == 'filterRule' || routerName == 'intelligent'){
                this.router4 = router;
            }
            else{
                this.router4 = '/home/intelligent'
            }
        },
    },
    watch: {
      activeNum: function () {
          var that = this;
          if(this.activeNum.length==2){
              let indexLi=this.activeNum.slice(0,1);
              /*$('.aside-item').eq(indexLi).addClass('active').siblings('.aside-item').removeClass('active');*/
          }
          else{
              $('.aside-item').each(function (index) {
                  if(index===that.activeNum){
                      /*$(this).siblings('.aside-item').removeClass('active');
                      $(this).addClass('active');*/
                      $('.child-list').removeClass('child-list-move');
                      $('.aside-bar').removeClass('aside-bar-move');
                  }

              });
          }
      },
      "$route":"fetchData"
  }
}

</script>

<style lang='scss'>
    $baseBlueColor: #436EF6;
    #home_aside{
        background: #dee2ed;
        width:100%;
        height: 100%;
        position: relative;
        .aside-bar{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: #343C45;
            transition: all 0.3s;
            z-index: 4;
            .aside-nav{
                font-size:18px;
                font-family:MicrosoftYaHei;
                background: #fff;
                .aside-item{
                    height: 100px;
                    line-height: 100px;
                    position: relative;
                    padding: 0;
                    a{
                        display: block;
                        .nav-name{
                            color: #000;
                            vertical-align: middle;
                        }
                        .nav-icon{
                            vertical-align: middle;
                            padding-right: 10px;
                            font-size: 20px;
                            color: $baseBlueColor;
                            padding-left: 12px;
                        }
                        .nav-icon2{
                            position: absolute;
                            right: 10px;
                            top: 0;
                            opacity: 0;
                            font-size: 20px;
                            color: $baseBlueColor;
                        }
                        &.active,&:hover{
                            background: #798CA1;
                            .nav-name,.nav-icon,.nav-icon2{
                                color: #fff;
                            }
                        }
                    }
                    &.active,&:hover{
                        background: #798CA1;
                        .nav-name,.nav-icon,.nav-icon2{
                            color: #fff;
                        }
                    }
                }
            }
        }
        .aside-bar-move{
            left: -118px;
            transition: all 0.3s;
            .aside-nav{
                .aside-item{
                    a{
                        .nav-name{
                            opacity: 0;
                        }
                        .nav-icon{
                            opacity: 0;
                        }
                        .nav-icon2{
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
    .aside-bar .aside-nav .active a .nav-icon2{
        color: #fff;
    }
    .child-list{
        //opacity: 0;
        height: 100%;
        width: 116px;
        font-size: 18px;
        position: absolute;
        left: 0;
        color: #000;
        background: #fff;
        z-index: -1;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }
    .child-list-move{
        //opacity: 1;
        z-index: 3;
        left: 44px;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }
    .toggle-btn-list{
        width: 16px;
        height: 40px;
        position: absolute;
        top: 50%;
        margin-top: -20px;
        right: 12px;
        cursor: pointer;
    }
    .child-name{
        height: 60px;
        line-height: 60px;
        text-align: center;
        &:hover,&.active{
            background: #798CA1;
            a{
                color: #fff;
            }
        }
    }
    .child-name a{
        display: block;
        font-size: 17px;
        color: #000;
        &.active{
            background: #798CA1;
            color: #fff;
        }
    }
    @media screen and (max-height: 800px) {
        .child-name {
            height: 50px;
            line-height: 50px;
        }
    }
    @media screen and (max-height: 800px) {/*当屏幕尺寸高度小于800px时，应用下面的CSS样式*/
        #home_aside {
            .aside-bar{
                .aside-nav {
                    font-size: 17px;
                    .aside-item {
                        height: 80px;
                        line-height: 80px;
                        //padding-left: 12px;
                    }
                }
            }
        }
        #home_aside .aside-bar .aside-nav .aside-item a .nav-icon2 {
            top: 5px;
        }
        .child-name {
            height: 60px;
            line-height: 60px;
        }
        @for $i from 0 through 6 {
            .child-list-#{$i} li:first-child { margin-top: 100px * $i; }
        }
    }
    @media (min-height: 801px) and (max-height: 900px) {/*当屏幕尺寸高度小于900px时，应用下面的CSS样式*/
        #home_aside {
            .aside-bar{
                .aside-nav {
                    font-size: 17px;
                    .aside-item {
                        height: 120px;
                        line-height: 120px;
                        padding-left: 12px;
                    }
                }
            }
        }
        #home_aside .aside-bar .aside-nav .aside-item a .nav-icon2 {
            top: 5px;
        }
    }
</style>
